// Types
import type { PhaseProps } from 'types/game';
// Hooks
import { useStep } from 'hooks/useStep';
import { useUser } from 'hooks/useUser';
// Utils
import { PHASES } from 'utils/phases';
// Icons
import { EvaluateIcon } from 'icons/EvaluateIcon';
// Components
import { Translate } from 'components/language';
import { PhaseAnnouncement, PhaseContainer } from 'components/phases';
import { StepSwitcher } from 'components/steps';
import { Instruction } from 'components/text';
// Internal
import { useOnSubmitGuessesAPIRequest } from './utils/api-requests';
import { ScoringRules } from './components/RulesBlobs';
import { StepGuessing } from './StepGuessing';

export function PhaseGuessing({ players, state }: PhaseProps) {
  const user = useUser(players, state);
  const { step, setStep } = useStep();

  const onSubmitGuesses = useOnSubmitGuessesAPIRequest(setStep);

  const announcement = (
    <PhaseAnnouncement
      icon={<EvaluateIcon />}
      title={
        <Translate pt="Pareie os personagens e símbolos" en="Pair characters with the players' glyphs" />
      }
      currentRound={state?.round?.current}
      type="overlay"
    >
      <Instruction>
        <ScoringRules currentRound={state.round.current} />
      </Instruction>
    </PhaseAnnouncement>
  );

  return (
    <PhaseContainer phase={state?.phase} allowedPhase={PHASES.QUEM_SOU_EU.GUESSING}>
      <StepSwitcher step={step} players={players}>
        {/* Step 0 */}
        <StepGuessing
          user={user}
          onSubmitGuesses={onSubmitGuesses}
          characters={state.characters}
          tableOrder={state.tableOrder}
          players={players}
          announcement={announcement}
          round={state.round}
          imageCardMode={state.mode === 'imageCards'}
        />
      </StepSwitcher>
    </PhaseContainer>
  );
}
